.AboutUs {
  display: flex;
  min-height: 100vh;
  align-items: center;
  margin: 0 100px;
}
.teamImg {
  width: 40vw;
}
.aboutUsWrapper {
  margin-bottom: 100px;
  margin-left: 50px;
}
.aboutUsWrapper p {
  color: #000000;
}
.theTeamDes {
  font-size: 17px;
  line-height: 25px;
  margin-top: 10px;
}
.teamTitle {
  font-size: 30px;
  padding-left: 50px;
  line-height: 50px;
  font-weight: bold;
}
.aboutpriorityPulse {
  color: #302b2b;
  font-size: 28px;
  font-weight: bold;
  padding-top: 30px;
  padding-left: 75px;
}
@media screen and (max-width: 1200px) {
  .AboutUs {
    margin: 0 60px;
  }
  .aboutUsWrapper {
    margin-bottom: 60px;
  }
}
@media screen and (max-width: 768px) {
  .AboutUs {
    margin: 0 20px;
  }
  .theTeamDes {
    font-size: 15px;
  }
}
@media screen and (max-width: 640px) {
  .aboutus-wrapper {
    margin-bottom: 10px;
  }
  .AboutUs {
    flex-direction: column;
    margin: 40px 10px;
  }
  .teamImg {
    width: 90vw;
  }
  .aboutUsWrapper {
    margin-left: 10px;
    margin-bottom: 20px;
    margin-top: -20px;
  }
  .teamTitle {
    text-align: center;
    padding-left: 0;
  }
  .theTeamDes {
    font-size: 18px;
  }
  .aboutpriorityPulse {
    padding-left: 30px;
    font-size: 20px;
  }
}
